<template>
    <div id="app">
        <div>
            <!-- 模拟注册接口 -->
            <div class="border">
                <h3>注册功能结果测试区</h3>
                <p>注册的结果: {{ registerCode }}, 信息: {{ registerMsg }}</p>
                <button class="b" @click="tAlert('已主动触发!')">进行拉码的响应注册</button>
                <!-- <button class="b" @click="register()">进行拉码的响应注册</button> -->
            </div>

            <!-- 模拟拉码响应 -->
            <div class="border">
                <h3>模拟拉码展示区</h3>
                <div v-if="(registerCode != '-1')">
                    <!-- qrcodeBase64: {{ qrcodeBase64 }} -->
                    <div v-if="showQrcodeImg">
                        <div class="qrcode-container">
                            <p>{{ qrIn }}</p>
                            <img class="qr-img" :src="qrcodeBase64" alt="拉码" />
                            <p>生码成功, 信息: {{ qrMsg }}</p>
                        </div>
                        <p>原始数据: {{ qrOriginData }}</p>
                    </div>
                    <div class="qd-error" v-else>
                        <p>
                            发生错误
                            <br />
                            错误code: {{ qrCode }}
                            <br />
                            额外信息: {{ qrMsg }}
                        </p>
                        <p>原始数据: {{ qrOriginData }}</p>
                        <button class="b" @click="handleQrCode()">模拟处理</button>
                        <p
                            class="small"
                            v-if="isPaymentTypeError"
                        >注意: 如果出现未签约或者不是默认支付方式，模拟处理会跳转支付管理界面；如需模拟操作，调用下面的签约或者设置默认模拟即可!</p>
                    </div>
                </div>
            </div>

            <!-- 模拟签约，请确保该账号确实存在 -->
            <div class="border" v-if="isPaymentTypeError">
                <h3>{{ qrCode == '8011' ? "签约测试区" : "设置默认支付方式测试区" }}</h3>
                <h4>原始错误数据区:</h4>
                <p>
                    发生错误！
                    <br />
                    错误code: {{ qrCode }}
                    <br />
                    额外信息: {{ qrMsg }}
                    <br />
                    原始数据: {{ qrOriginData }}
                </p>
                <h4>处理回复区:</h4>
                <p>
                    设置默认结果:
                    <br />
                    处理返回的code: {{ typeCode }}
                    <br />
                    处理返回的Msg: {{ typeMsg }}
                    <br />
                    处理返回的原始数据: {{ typeOriginData }}
                </p>
                <button class="b" @click="handleSignOrDefault()">{{ signHandleTitle }}</button>
            </div>

            <!-- 模拟异常的主动处理 -->
            <div class="border">
                <h3>灵活根据异常Code进行模拟测试区</h3>
                <input
                    class="code-input"
                    v-model="handleInputCode"
                    type="text"
                    placeholder="请谈些模拟code"
                />
                <p>处理的结果: {{ handleCode }}:{{ handleBackCode }}, 处理后的返回信息: {{ handleMsg }}</p>
                <button class="b" @click="handle()">进行拉码的响应处理</button>
            </div>

            <!-- 模拟的网络请求 -->
            <div class="border">
                <h3>模拟网络请求测试区</h3>
                <p>请求的URL: {{ requestURL }}</p>
                <p>
                    请求的参数
                    <span class="small">(使用SDK的默认参数)</span>
                    : {{ requestParam }}
                </p>
                <p class="url">请求返回的数据: {{ responseAllData }}</p>
                <button class="b" @click="request()">使用SDK进行网络请求</button>
            </div>

            <!-- 模拟调用SDK打开新的网页 -->
            <div class="border">
                <h3>模拟打开新界面(广告业)测试区</h3>
                <p>打开的HTML: {{ webURL }}</p>
                <p class="url">请求返回的数据: {{ webResponseObj }}</p>
                <button class="b" @click="openURL()">使用SDK重新开启一个内部浏览器</button>
            </div>

            <!-- 模拟拿到SDK统计数据 -->
            <div class="border">
                <h3>SDK统计数据测试区</h3>
                <p>统计的数据: {{ navtive }}</p>
                <button class="b" @click="getNavtiveMessage()">获得SDK统计的相关数据</button>
            </div>
        </div>
    </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'

export default {
    name: 'App',
    components: {
    },
    data: function () {
        return {
            // progress: "0%",
            // result: "-1",
            // msg: "",
            // base64: "",
            // checkHandelrObj

            //MARK: 注册结果
            registerCode: "-1",
            registerMsg: "******",

            //二维码
            qrCode: "",
            qrMsg: "",
            qrImg: "",
            qrOriginData: "",
            qrIn: "",

            //处理的code回调
            handleInputCode: "",
            handleCode: "-1",
            handleMsg: "******",
            handleBackCode: "",

            //网络请求
            requestURL: "http://dtcustomer.bestonepay.com/ngqeb/thirdParty/init",
            requestParam: {},
            responseAllData: {},

            //打开web
            webURL: "https://www.baidu.com",
            webResponseObj: "",


            //签约以及设置默认区
            typeCode: "",
            typeMsg: "",
            typeOriginData: "",
            // isSign: false,

            //SDK数据获得区
            navtive: {}
        }
    },
    computed: {

        qrcodeBase64() {
            if (this.qrImg == '') {
                return ""
            }
            return "data:image/jpg;base64," + this.qrImg
        },

        showQrcodeImg() {
            return !(this.qrcodeBase64 == '')
        },

        //签约模拟的title
        signHandleTitle() {
            return this.qrCode == '7005' ? "设置支付宝为默认" : "开始签约支付宝"
        },

        //是否是签约或者默认错误
        isPaymentTypeError() {
            return this.qrCode == '7005' || this.qrCode == '8011';
        }
    },

    methods: {

        tAlert(msg) {
            alert(msg)
        },

        register() {
            const message = {
                check: "checkHandler",    //必填，拉码前置的状态检测
                qrcode: "qrcodeHandler", //必填，拉码成功后的图片回调
                qrcodeSize: 220,         //选填，生成图片的大小，iOS需要按照1倍图的尺寸，单位pt，默认为220pt
                callBack: "regCallBack"  //可选，用于返回此方法的调用结果回调
            }

            /// 用于前置异常状态的回调
            window.checkHandler = (obj) => {
                // alert(obj.msg)
                this.qrImg = ""
                this.qrCode = obj.code
                this.qrMsg = obj.msg
                this.qrOriginData = obj.originData
                //obj.code   //用于标记当前的状态，点击按钮时可用于透传，UI需要根据此code进行主动刷新变化
                //obj.msg    //用于调试的文本通信
                //obj.originData //可以不选择，此处返回的是客户端拿到后验签通过服务端返回json字符串
            }


            /// 用于二维码的处理
            window.qrcodeHandler = (obj) => {
                this.qrCode = "0000" //将qrCode清除为默认的成功

                this.qrImg = obj.base64
                this.qrMsg = obj.msg
                this.qrOriginData = obj.originData
                this.qrIn = obj.isIn == '1' ? "进站码" : "出站码"
                //obj.base64 //如果生码成功，将返回二维码图片的base64; 不存在前缀，ex: data:image/jpg;base64)；如果生码失败，为undefined
                //obj.msg    //用于调试
                //obj.isIn   //是否是进站码
                //obj.originData //原始数据 1是进站  0是出站码
            }


            window.regCallBack = (obj) => {
                //obj.code //注册的结果  1为成功 0为失败
                this.registerCode = obj.code
                //obj.msg  //注册结果信息
                this.registerMsg = obj.msg
            }


            window.webkit.messageHandlers.QMD_CODE_REGISTER.postMessage(message)
        },

        //处理接收的异常码
        handleQrCode() {
            const message = {
                code: this.qrCode,      //需要客户端处理的code，通过存储的被动code进行透传
                handle: "handleCallBack"   //可选，用于返回处理结果
            }

            this.qrMsg = "正在交予SDK处理改Code"

            window.handleCallBack = (obj) => {
                /// 显示的数据
                const msg = "SDK处理的编码为:" + obj.isHandle + "  (" + (obj.isHandle == "1" ? "处理成功" : "处理失败") + ") 返回信息为：" + obj.msg
                this.qrMsg = msg

                //obj.code   //回调的客户端处理的code
                //obj.msg    //用于调试的信息
                //obj.isHandle //是否已经处理完成，1为已经处理，0为不能处理
            }

            window.webkit.messageHandlers.QMD_CODE_HANDER.postMessage(message)
        },

        handle() {
            const message = {
                code: this.handleInputCode,      //需要客户端处理的code，通过存储的被动code进行透传
                handle: "handleCallBack"   //可选，用于返回处理结果
            }

            window.handleCallBack = (obj) => {
                this.handleMsg = obj.msg
                this.handleCode = obj.code
                this.handleBackCode = obj.isHandle
                //obj.code   //回调的处理状态
                //obj.msg    //用于调试的信息
                //obj.isHandle //是否已经处理完成，1为已经处理，0为不能处理
            }

            window.webkit.messageHandlers.QMD_CODE_HANDER.postMessage(message)
        },

        request() {
            const message = {
                url: this.requestURL,      //必填，需要客户端请求的URL
                params: {     //必填，需要请求的额外参数，用户等数据均有SDK进行拼接,不接收复杂的数据结构，value全部使用String
                    "isTest": "1"
                },
                //如果不存在额外的参数，使用空对象即可
                // params: {}
                timeoutInterval: 0.5,
                callBack: "responseHandler"
            }

            window.responseHandler = (obj) => {
                this.responseAllData = obj
                //obj.code   //1表示成功，0表示失败
                //obj.msg    //用于调试的信息
                //obj.data   //网络请求的数据,失败时为undefined
            }
            window.webkit.messageHandlers.QMD_REQUEST.postMessage(message)
        },

        openURL() {
            const message = {
                url: this.webURL,       //必填，客户端需要加载的URL
                title: "我是固定的标题",    //选填，如果不需要固定死标题栏，不需要传递该参数
                callBack: "openCallBack"
            }

            window.openCallBack = (obj) => {
                //obj.code   //1表示成功，0表示失败
                this.webResponseObj = obj
            }

            window.webkit.messageHandlers.QMD_WEB.postMessage(message)
        },


        //处理签约以及设置默认
        handleSignOrDefault() {
            //进行设置默认
            if (this.qrCode == "7005") {
                //设置默认方式由H5自行发起SDK的请求
                const message = {
                    //测试环境的url/正式环境需要切换
                    url: "http://dtcustomer.bestonepay.com/ngqeb/thirdParty/switchDefault",  //必填，需要客户端请求的URL
                    // params: {     //必填，需要请求的额外参数，用户等数据均有SDK进行拼接,不接收复杂的数据结构，value全部使用String
                    //     "param1": "",
                    //     "param2": "",
                    // },
                    //如果不存在额外的参数，使用空对象即可
                    params: {},
                    timeoutInterval: 4, //选填，超时时间，单位为s,不填默认SDK为4s
                    callBack: "responseHandler"
                }

                window.responseHandler = (obj) => {

                    //obj.code   //1表示成功，0表示失败
                    //obj.msg    //用于调试的信息
                    //obj.data   //网络请求的数据

                    this.typeCode = obj.code
                    this.typeMsg = obj.msg
                    this.typeOriginData = obj.data

                    if (obj.code == '1') {

                        //需要界面重启启用普通刷新 （此方法为模拟界面方法，因为统一界面需要刷新）
                        window.webkit.messageHandlers.QMD_CODE_HANDER.postMessage({
                            code: "90002"
                        })

                        //如果是支付管理，只需要重新自助刷新此界面即可
                        //reload()
                    }

                }

                window.webkit.messageHandlers.QMD_REQUEST.postMessage(message)
                return
            }

            //设置默认支付方式
            const message = {
                callBack: "signCallBack"   //可选，用于返回处理结果
            }

            window.signCallBack = (obj) => {
                this.typeCode = obj.code
                this.typeMsg = obj.msg
                //obj.code   //回调的处理状态
                //obj.msg    //用于调试的信息
            }

            window.webkit.messageHandlers.QDM_SIGN.postMessage(message)

        },

        //获得原生SDK宿主的相关信息
        getNavtiveMessage() {

            const message = {
                //接收数据的回调
                callBack: "catchNativeMessage"
            }

            window.catchNativeMessage = (obj) => {
                //obj为json对象
                this.navtive = obj
            }

            window.webkit.messageHandlers.QMD_NATIVE.postMessage(message)
        }
    },

    created() {
        document.title = "iOS交互测试"
    },

    mounted() {
        this.register();//主动触发注册

    }
}
</script>

<style slot-scope="less">
#app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 10px 0;
}

button {
  /* background-color: rebeccapurple; */
  height: 88px;
}

.b {
  height: 50px;
}

.border {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid rgb(176, 176, 176);
  word-break: break-all;
}

.small {
  color: red;
  font-size: 10px;
}

.qd-error {
  display: flex;
  flex-direction: column;
}

.qr-img {
  align-self: center;
  width: 220px;
  height: 220px;
}

.code-input {
  height: 44px;
}

.qrcode-container {
  display: flex;
  flex-direction: column;
  text-align: center;
}
</style>
